.days{
    color: black;
    margin-top:20px;
    margin-bottom: 20px;
    width:300px;
    height: 610px;
    border: 2px solid black;
    background-image: url(/Weather-Updates/static/media/weather-background.f5d531f8.jpg);
    background-size: cover; 
    position: relative;
    display: inline-block;
    overflow: hidden;
    border-radius: 7px;
    margin: 60px 15px;
    transition: .7s all cubic-bezier(0.785, 0.135, 0.15, 0.86);
    box-shadow: 0 50px 150px -20px rgba(0,0,0,.75);
}

.days:before {
    position: absolute;
    -webkit-transform: rotate(30deg);
            transform: rotate(30deg);
    top: -40px;
    left: -200px;
    transition: 1s all cubic-bezier(0.785, 0.135, 0.15, 0.86);
}

.days:hover{
    cursor: pointer;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    border-radius: 10px;
    box-shadow: 0 20px 300px -20px rgba(0,0,0,1);
}

button {
    padding: 10px 20px;
    transition: all 0.5s;
    border: 0 none;
    border-radius: 5em;
    text-transform: uppercase;
    font-size: 18px;
    margin-right: 20px;
    margin-top:30px;
}

.action {
    background-color: rgba(43, 247, 70, 0.829);
    background-image: linear-gradient(rgb(199, 13, 100), rgb(190, 11, 95));
    background-position: 50% 50%;
    background-repeat: no-repeat;
    background-size: 0% 100%;
    outline: none;
}
  
button:hover { 
    color: white;
}

.action:hover {
    background-size: 100% 100%; 
    cursor: pointer;
    outline: none;
}

.text{
    font-size: 24px;
    color:white;
    margin-top:30px;
}

.clock{
    margin-top:10px;
    font-size: 70px;
}

.date, .location-input{
    margin-top:30px;
    font-size: 50px;
}
.input{
    width: 300px;
    height: 30px;
    font-size: 24px;
    text-align: center;
    outline: none;
}
